import React, { useState, useEffect, useRef } from 'react';
import { H2, Marginbottom } from './style/Commonui';
import './storeposition.scss'

function Storeposition(props) {
  const [mapinfo, setMapInfo] = useState(0);
  const mapElement = useRef(null);
  const markerData = [
    { title: " ", lat: 37.53861255128007, lng: 126.73558946924486, posi: "   71 D 1 120, 121" },
    { title: " ", lat: 36.992232800115346, lng: 127.13794333462208, posi: "    20 1 105" },
    { title: " ", lat: 37.85252910020382, lng: 127.74549773462198, posi: "   2025  1  " },
    { title: " ", lat: 36.3398422000005, lng: 127.3825631539336, posi: "   367 1" },
    { title: " ", lat: 36.76958426861356, lng: 127.04689522696928, posi: "    58-44 224 1 202" },
    { title: " ", lat: 37.508417550133835, lng: 126.73270483462234, posi: "   14 1 104" },
    { title: " ", lat: 35.79568295001999, lng: 127.13842443462188, posi: "   1 24 1  " },
    { title: " ", lat: 37.83896840029818, lng: 127.08986928462181, posi: "   1657 1 105" },
    { title: " ", lat: 37.501550800253085, lng: 126.76879308462219, posi: "    108" },
    { title: " ", lat: 36.35888225025092, lng: 127.34856458462261, posi: "   117 1 101" },
  ];

  useEffect(() => {
    const script = document.createElement('script');
    script.src = `https://openapi.map.naver.com/openapi/v3/maps.js?ncpClientId=${process.env.REACT_APP_NAVER_MAP_KEY}`;
    script.async = true;
    script.onload = () => initializeMap();
    document.head.appendChild(script);
  }, []);

  const initializeMap = () => {
    const { naver } = window;
    if (!naver || !naver.maps) {
      console.error('Naver Maps API is not loaded.');
      return;
    }

    const location = new naver.maps.LatLng(markerData[mapinfo].lat, markerData[mapinfo].lng);
    const mapOptions = {
      center: location,
      scrollWheel: false,
      zoom: 16,
      zoomControl: true,
      zoomControlOptions: {
        position: naver.maps.Position.TOP_RIGHT,
      },
    };
    const map = new naver.maps.Map(mapElement.current, mapOptions);

    markerData.forEach((data) => {
      new naver.maps.Marker({
        position: new naver.maps.LatLng(data.lat, data.lng),
        map: map,
        title: data.title,
      });
    });
  };

  useEffect(() => {
    if (window.naver && window.naver.maps) {
      initializeMap();
    }
  }, [mapinfo]);

  console.log(process.env.REACT_APP_NAVER_MAP_KEY)

  return (
    <>
      <div id={props.keyid}>
        <Marginbottom>
          <H2></H2>
          <section className='storeposi'>
            {/* <div id="map" style={{ width: "90vw", height: "80vh" }}></div> */}
            <div id='storemap' ref={mapElement} style={{ width: "100vw", height: "80vh" }}>

              <div className='storediv position-relative'>
                <div className='storelist'>
                  <div className='storelistinput border-bottom'>
                    <input type="text" placeholder='' className='w-100' />
                    <button><i className="bi bi-search"></i></button>
                  </div>
                  {
                    markerData.map((el, idx) => {
                      return (
                        <React.Fragment key={`list${idx}`}>
                          <div className={`d-flex imglist list${idx}`} onClick={() => { setMapInfo(idx) }}>
                            <img className='d-none d-lg-block' src="/img/store/positionpic.png" alt="" />
                            <div>
                              <h4>{el.title}</h4>
                              <p className='d-none d-lg-block'>{el.posi}</p>
                            </div>
                          </div>
                        </React.Fragment>
                      )
                    })
                  }
                </div>
              </div>
            </div>
          </section>
        </Marginbottom>
      </div>
    </>
  );
}

export default Storeposition;
